import { Operand } from "../types/CommonsTypes"
import {
  MISSING_OPERAND_A,
  MISSING_OPERAND_B,
  MISSING_OPERAND_OBJECT,
} from "../constants"

/**
 * @description Check arithmetic functions errors
 *
 * @param {Operand} operand Operands
 * @param {string} functionName function name
 *
 * @return {void}
 */
export function checkCommonsError(
  operand: Operand,
  functionName: string
): void {
  const { a, b } = operand

  if (!operand) throw Error(`${functionName} ${MISSING_OPERAND_OBJECT}`)
  if (!a) throw Error(`${functionName} ${MISSING_OPERAND_A}`)
  if (!b) throw Error(`${functionName} ${MISSING_OPERAND_B}`)
}
